<!---->
<template>
  <div class="container-box">
    <PageHeader>
      <template v-slot:left>
        <InputSearch
          :placeholder="''"
          @changeInputSearch="changeInputSearch(inputVal)"
        ></InputSearch>
        <DateSearch
          class="header-btn-list"
          @changeDateSearch="changeDateSearch(date)"
        />
        <SelectSearch
          class="header-btn-list"
          :placeholder="''"
          :dataListOpt="dataListOpt"
          @changeSelectSearch="changeSelectSearch(selectVal)"
        ></SelectSearch>
        <HeaderBtn class="header-btn-list"></HeaderBtn>
      </template>
      <template v-slot:right>
        <HeaderBtn :isPlain="false" @click="add()" class="header-btn-list"
          ></HeaderBtn
        >
        <HeaderBtn class="header-btn-list" :type="'danger'"></HeaderBtn>
      </template>
    </PageHeader>
    <!--  -->
    <main>
      <BaseTable
        :isSelection="true"
        :tableData="tableData"
        :tableColumns="tableColumns"
        :operatorList="operatorList"
        @handleOperator="handleOperator"
      >
      </BaseTable>
    </main>
    <!--  -->
    <Forms ref="Forms" :title="formTitle" />
  </div>
</template>
<script>
import PageHeader from "@/components/page-header/index.vue";
import InputSearch from "@/components/search/input-search.vue";
import SelectSearch from "@/components/search/select-search.vue";
import DateSearch from "@/components/search/date-search.vue";
import HeaderBtn from "@/components/btn/btn.vue";
import BaseTable from "@/components/table/base-table.vue";
import Forms from "./form.vue";
export default {
  data() {
    return {
      formTitle: "",
      tableColumns: {
        columns: [
          {
            prop: "seqNum",
            key: "seqNum",
            title: "",
            width: "50",
          },
          {
            prop: "bianhao",
            key: "bianhao",
            title: "",
          },
          {
            prop: "bianhao1",
            key: "bianhao1",
            title: "",
          },
          {
            prop: "bianhao2",
            key: "bianhao2",
            title: "",
          },
          {
            prop: "bianhao3",
            key: "bianhao3",
            title: "",
          },
          {
            prop: "bianhao4",
            key: "bianhao4",
            title: "",
          },
          {
            prop: "bianhao5",
            key: "bianhao5",
            title: "",
          },
          {
            prop: "bianhao6",
            key: "bianhao6",
            title: "",
          },
        ],
        operator: {
          prop: "operator",
          key: "operator",
          title: "",
          width: "200",
        },
      },
      tableData: [
        {
          seqNum: 1,
          bianhao: "",
          bianhao1: "2021-12-25",
          bianhao2: "3435565767",
          bianhao3: "14,032.12",
          bianhao4: "3%",
          bianhao5: "14,032.12",
          bianhao6: "14,032.12",
        },
      ],
      operatorList: [
        { title: "", key: "viewVoucher" },
        { title: "", key: "edit" },
        { title: "", key: "delete" },
      ],
      dataListOpt: [
        {
          value: "1",
        },
      ],
    };
  },
  components: {
    PageHeader,
    InputSearch,
    SelectSearch,
    HeaderBtn,
    DateSearch,
    BaseTable,
    Forms,
  },
  methods: {
    changeDateSearch(date) {},
    changeInputSearch(inputVal) {},
    changeSelectSearch(selectVal) {},
    add() {
      this.$refs["Forms"].isVisible = true;
      this.formTitle = "";
    },
    handleOperator(item, row) {
      switch (item.key) {
        case "viewVoucher":
          break;
        case "edit":
          this.$refs["Forms"].isVisible = true;
          this.formTitle = item.title;
          break;
        case "delete":
          break;
      }
    },
  },
};
</script>
<style scoped lang="scss">
</style>